import React from 'react';

import { componentToHtml } from '@miq/editor';
import Form, { useForm } from '@miq/form';
import { Img, ImgUploadButton, Picture } from '@miq/components';

import { AdminView } from '../views';
import { siteServices } from './utils';

export const ClosePageRender = (props) => {
  const { ct_title, ct_text, ct_image_data } = props;

  return (
    <div className="close-page">
      <Picture {...ct_image_data} />
      <div className="close-page-inner">
        <div className="text-center p-3">
          {ct_title && <h1 className="close-template-title mb-1">{ct_title}</h1>}
          {ct_text && <h3 className="close-template-text">{ct_text}</h3>}
        </div>
      </div>
    </div>
  );
};

const ClosePageForm = ({ setting = {}, ...props }) => {
  const { toast, setSetting } = props;
  const form = useForm({ ct_title: setting.ct_title || '', ct_text: setting.ct_text || '' });
  const { slug, ct_html } = setting;

  const handleSave = ({ name, value }) => {
    if (!slug) return;
    const data = { ...setting, [name]: value };

    return siteServices
      .patch(slug, { [name]: value, ct_html: html }, { [name]: setting[name], ct_html })
      .then(({ isUpdated, ...settingData }) => {
        if (isUpdated) {
          setSetting && setSetting(settingData);
        }
      })
      .catch((err) => form.handleError(err));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const ct_html = componentToHtml(<ClosePageRender {...{ ...setting, ...form.values }} />);

    return siteServices
      .patch(slug, { ...form.values, ct_html })
      .then(({ isUpdated, ...settingData }) => {
        if (isUpdated) {
          setSetting && setSetting(settingData);
          toast && toast.success({ message: 'Page updated.' });
        }
      })
      .catch((err) => form.handleError(err));
  };

  const handleImageCreate = (imgData) => {
    const data = { ...setting, ct_image_data: imgData };

    const html = componentToHtml(<ClosePageRender {...data} />);
    return siteServices.patch(slug, { ct_image: imgData.slug, ct_html: html }).then(({ isUpdated, ...res }) => {
      if (isUpdated) {
        setSetting && setSetting(siteData);
        toast && toast.success({ message: 'Image added.' });
      }
    });
  };

  const handleImageUpdate = ({ isUpdated, ...imgData }) => {
    const data = { ...setting, ct_image_data: imgData };
    const html = componentToHtml(<ClosePageRender {...data} />);
    return siteServices.patch(slug, { ct_html: html }).then(({ isUpdated, ...res }) => {
      if (isUpdated) {
        setSetting(res);
        toast.success({ message: 'Image updated.' });
      }
    });
  };

  return (
    <Form context={form} onSubmit={handleSubmit} className="setting-close-page-form">
      <div className="text-bold">Customize your landing.</div>

      <div className="mb-1">
        <Form.TextInput
          name="ct_title"
          placeholder="Write title ..."
          // onSave={handleSave}
          error={form.errors.ct_title}
          maxLength={50}
        />
      </div>

      <div className="mb-1">
        <Form.TextArea
          name="ct_text"
          placeholder="Write text ..."
          // onSave={handleSave}
          error={form.errors.ct_text}
          maxLength={99}
        />
      </div>

      <div className="mb-2">
        <Form.Submit value="Save" className="btn btn-primary-2" />
      </div>

      <div className="preview d-grid grid-3">
        <div className="span-2 pe-1">
          <p className="fw-bold mb-1">Desktop Preview</p>
          <ImgUploadButton
            slug={setting.ct_image ? setting.ct_image_data.slug : null}
            onCreate={handleImageCreate}
            onUpdate={handleImageUpdate}
          >
            {setting.ct_image && <Img {...setting.ct_image_data} />}
          </ImgUploadButton>
        </div>

        {setting.ct_image && (
          <div className="mobile span-1">
            <p className="fw-bold mb-1">Mobile Preview</p>
            <ImgUploadButton
              isMobile
              slug={setting.ct_image ? setting.ct_image_data.slug : null}
              onCreate={handleImageCreate}
              onUpdate={handleImageUpdate}
            >
              <Img isMobile {...setting.ct_image_data} />
            </ImgUploadButton>
          </div>
        )}
      </div>
    </Form>
  );
};

export default function SiteLiveSettingForm({ setting = {}, ...props }) {
  const { toast, setSetting } = props;
  const form = useForm({ is_live: setting.is_live || false });

  if (!setting.slug) return null;

  const handleSubmit = (e) => {
    e.preventDefault();

    return siteServices
      .patch(setting.slug, { is_live: form.values.is_live }, { is_live: setting.is_live })
      .then(({ isUpdated, ...data }) => {
        if (isUpdated) {
          setSetting && setSetting(data);
          toast &&
            toast.success({ message: data.is_live ? 'This site is open' : 'No one but you can see the content.' });
        }
      })
      .catch((err) => form.handleError(err));
  };

  const label = setting && setting.is_live ? 'This site is open to the public' : 'This site is close to the public.';

  return (
    <AdminView.Section title="Site Status" text="Lock you site">
      <Form context={form} onSubmit={handleSubmit} className="mb-2">
        <Form.CheckboxInput name="is_live" checked={form.values.is_live} error={form.errors.is_live} label={label} />

        <div className="">
          <Form.Submit value="Save" className="btn btn-primary-2" />
        </div>
      </Form>

      {!setting.is_live && <ClosePageForm {...props} setting={setting} />}
    </AdminView.Section>
  );
}
