import { lazy } from 'react';

import { Switch } from 'react-router-dom';

import { AdminRoute } from '@miq/adminjs';

import './style.scss';

const StaffSettingIndexView = lazy(() => import('./IndexView'));
const StaffSettingsGeneralView = lazy(() => import('./GeneralSettingsUpdateView'));
const StaffSiteIndexUpdateView = lazy(() => import('./IndexSettingsUpdateView'));
const StaffNavSettingsUpdateView = lazy(() => import('./NavSettingsUpdateView'));

export default function SettingsLayout(props) {
  const { path } = props.match;

  return (
    <Switch>
      <AdminRoute
        path={`${path}general/`}
        component={StaffSettingsGeneralView}
        // requiredPerms={['shop.view_product']}
      />
      <AdminRoute
        path={`${path}index/`}
        component={StaffSiteIndexUpdateView}
        // requiredPerms={['shop.view_product']}
      />
      <AdminRoute
        path={`${path}nav/`}
        component={StaffNavSettingsUpdateView}
        // requiredPerms={['shop.view_product']}
      />
      <AdminRoute
        path={path}
        component={StaffSettingIndexView}
        //  requiredPerms={['shop.view_product']}
      />
    </Switch>
  );
}
